@import "variables";

@font-face {
    font-family: 'UJA';
    src: url('../../fonts/uja/uja.woff2') format("woff2"),
    url('../../fonts/uja/uja.woff') format("woff"),
    url('../../fonts/uja/uja.ttf') format("truetype"),
    url('../../fonts/uja/uja.svg#UJA') format("svg");
    font-weight: 400;
    font-style: normal
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
    @font-face {
        font-family: 'UJA';
        src: url('../../fonts/uja/uja.svg#UJA') format("svg")
    }
}

/* Font Name: Maison Neue
